<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        index: 0,
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Howdy. My Name is Nick Gejadze and
* I heard in order to get hired, 
* I need to prepare my Résumé.
* So, I decided to code it live.
*/

/* Let's start by animating elements */
* {
  transition: all 1s;
  -webkit-transition: all 1s;
}

/* This didn't do much yet, but it will.
 * Trust me, I am a developer.
 */


/* I like colors, let's add some colors */
html {
  color: rgb(222,222,222); 
  background-color: rgb(26,26,26);
}

/* We need to position the editor so we  
 * have the room for a Résumé.
 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 90vw;
  margin: 2.5vh 5vw;
  height: 90vh;
}
/* meh. this is too big, let's adjust 
 * it a little more */

.styleEditor {
  height: 45vh;
}

/* Add more colors */
.token.selector{ color: rgb(255,0,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: rgb(0,255,255); }
.token.function{ color: rgb(51, 152, 200); }

/* Tilt the editor a little */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  transform: rotateX(-10deg) 
             translateZ(-50px) ;
}

/* Now, let's start building 
 * the Résumé */
.resumeEditor{
  position: fixed;
  top: 50%; left: 0;
  padding: .5em;  margin: 2.5vh;
  width: 90vw; height: 45vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/* hm. I need to write 
 * something about me */


`,
          `
/* Markup is cool, but HR will be like
 * WHAT DA HACK IS THIS?
 * Let's convert it to HTML and make it 
 * pretty!
 */
`
          ,
          `
/* Here we go again, 
 * let's add some spacing */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  color: #0081ff;
  font-size: 110%;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ccc;
}

/* Ok, Looks good now. */
.styleEditor {
  height: 20vh;
}
.resumeEditor{
  top: 21%;
  height: 75vh;
}
`],
        currentMarkdown: '',
        fullMarkdown: `Nikoloz Gejadze
====

##### Making the web a better place

QUALIFICATIONS
----

* Pascal
* C++
* Python
* Javascript
* PHP 
* HTML 4/5, CSS 2/3
* Hadoop
* Kubernetes
* JBase
* MySQL
* PostgreSQL
* SQLite
* Redis
* Memcache
* MongoDB
* CouchDB
* Sphinx
* Apache Cassandra
* Aerospike
* Server and Network Administration


ACHIEVEMENTS 
----

* 1st prize winner of Programming contests at Georgian Technical University.
* Delivered presentations on Cyber Security to a large audiences.


LANGUAGES
----

* Georgian
* English
* Russian


CONTACT
----
* Tbilisi, Georgia
* +(995) 555 04 22 00
* [GitHub](https://github.com/ngfw/)
* Email: gejadze at gmail.com

> Old Scool Résumé, can be [Downloaded here](https://ngfw.github.com/nick_gejadze_resume.pdf)
`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          this.$nextTick(() => {
            this.$refs.resumeEditor.goTop()
          })
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            this.index++
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              if( this.index % (Math.floor(Math.random() * (60 - 10 + 1)) + 10) == 0){
                setTimeout(showStyle, Math.floor(Math.random() * (600 - 200 + 1)) + 200)
              }else{
                setTimeout(showStyle, Math.floor(Math.random() * (80 - 20 + 1)) + 20)
              }
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.index++
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              if( this.index % (Math.floor(Math.random() * (60 - 10 + 1)) + 10) == 0){
                setTimeout(showResume, Math.floor(Math.random() * (600 - 200 + 1)) + 200)
              }else{
                setTimeout(showResume, Math.floor(Math.random() * (90 - 30 + 1)) + 30)
              }
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }

</style>



